
html {
  background-color: #3D464D;
  font-family: BlinkMacSystemFont, -apple-system, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", "Helvetica", "Arial", sans-serif;
  font-size: 1rem;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  min-width: 300px;
  overflow-x: hidden;
  overflow-y: scroll;
}

/*! minireset.css v0.0.2 | MIT License | github.com/jgthms/minireset.css */
html,
body,
p,
ol,
ul,
li,
dl,
dt,
dd,
blockquote,
figure,
fieldset,
legend,
textarea,
pre,
iframe,
hr,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}
ul {
  list-style: none;
}
button,
input,
select,
textarea {
  margin: 0;
}
html {
  box-sizing: border-box;
}
* {
  box-sizing: inherit;
}
*:before, *:after {
    box-sizing: inherit;
}
img,
embed,
object,
audio,
video {
  max-width: 100%;
}
iframe {
  border: 0;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
td,
th {
  padding: 0;
  text-align: left;
}
body {
  color: black;
  font-size: 1rem;
  font-weight: normal;
  line-height: 1.5;
  overflow-x: hidden;
}
#app {
  background-color: white;
  margin: 0 auto;
  height: 100vh;
  width: 80vw;
  min-width: 20em;
  overflow: hidden;
  position: relative;
}
.logo {
  fill: white;
  position: absolute;
  z-index: 99;
  width: 60px;
  height: auto;
  top: 0;
  left: 0;
  right: 0;
  margin: 0 auto;
  transition: fill 300ms defaultEasing;
}
.logo.opened {
    fill: black;
}
.menu-link {
  background: none;
  border: none;
  padding: 0.2em;
}
.menu-label {
  color: white;
  text-transform: none;
  position: absolute;
  font-size: 14px;
  top: 34px;
  left: 50px;
  z-index: 9;
}
.menu-label:hover {
    cursor: pointer;
}
.icon-menu-open,
.icon-menu-close {
  stroke: white;
  stroke-width: 1.5;
}
.icon-menu-close {
  visibility: hidden;
}
.opened {
  color: black;
}
.opened .icon-menu-open,
  .opened .icon-menu-close {
    stroke: black;
}
.opened .icon-menu-open {
    visibility: hidden;
}
.opened .icon-menu-close {
    visibility: visible;
}
button {
  cursor: pointer;
}
button:focus {
    outline: none;
}
.content,
.nav-content {
  background-color: white;
  height: 100vh;
  width: 100%;
  padding: 2em 0 0;
  position: absolute;
  top: 0;
  left: 0;
}
.menus {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  margin: 130px 0 0 4vw;
  position: relative;
  z-index: 99;
}
.menu-primary {
  display: inline-block;
  -webkit-box-flex: 0;
      -ms-flex: 0 1 auto;
          flex: 0 1 auto;
  line-height: 2;
  margin: 0 4vw 0 0;
  position: relative;
  z-index: 6;
}
.menu-primary li {
    opacity: 0;
}
.menu-secondary {
  display: inline-block;
  -webkit-box-flex: 1;
      -ms-flex: 1 1 auto;
          flex: 1 1 auto;
  line-height: 2.4;
  margin: -3px 0 0;
  position: relative;
  z-index: 6;
}
.menu-secondary li {
    opacity: 0;
}
.menu-secondary li a {
      color: #878080;
      font-size: 0.8em;
      text-transform: uppercase;
}
.nav-content {
  left: -80vw;
  z-index: 5;
}
.content {
  background-position: top center;
  background-repeat: no-repeat;
  background-size: cover;
}
a {
  color: black;
  cursor: pointer;
  text-decoration: none;
}
a:hover {
    color: black;
}
a:visited {
    color: black;
}
.is-hidden {
  display: none !important;
}



